// import Vue from 'vue'
import Router from 'vue-router'
const home=resolve => require(['../components/page/home.vue'], resolve);
const litemPage=resolve => require(['../components/page/litemPage.vue'], resolve);


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect:'/home'
    },
    {
      path: '/home',
      name: 'home',
      component: home,
      meta:{
        title:'淘金金'
      }
    },
    {
      path: '/litemPage',
      name: 'litemPage',
      component: litemPage,
      meta:{
        title:'详情'
      }
    }
  ]
})



