<template>
  <div id="app">
    <keep-alive>
      <router-view v-if="$route.meta.keepAlive"></router-view>
    </keep-alive>
    <router-view v-if="!$route.meta.keepAlive"></router-view>

    <div class="loading" v-show="showloading">
      <van-loading color="white"/>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import { Loading } from 'vant';

Vue.use(Loading);
export default {
  name: "App",
  computed: mapGetters(["showloading"])
};
</script>
<style lang="stylus">
.loading {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 111;
  width: 100vw;
  height: 100vh;
  text-align: center;

  .van-loading {
    color: white;
    background: rgba(0, 0, 0, 0.5);
    width: 50px;
    height: 50px;
    padding: 40px;
    margin: auto;
    margin-top: 68%;
    z-index: 0;
    font-size: 0;
    line-height: 0;
    position: relative;
    vertical-align: middle;
  }
}
</style>


